:root {
  --color-primary-blue: #2f84ea;
  --color-off-white: #f7f7f7;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  height: 100%;
}

body {
  font-family: "Lato", sans-serif;
  height: 100%;
  overflow-x: hidden;
}

#root {
  height: 100%;
}
